.hero {
  display: flex;
  justify-content: space-between;
}

.blur-h {
  width: 30rem;
  height: 33rem;
  left: 0;
}

.left-h {
  padding: 2rem;
  padding-top: 1.5rem;
  flex: 3;
  display: flex;
  gap: 2rem;
  flex-direction: column;
}

.the-best-ad {
  margin-top: 4rem;
  background-color: #363d42;
  border-radius: 4rem;
  width: fit-content;
  padding: 20px 30px;
  text-transform: uppercase;
  color: white;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.the-best-ad > span {
  z-index: 2;
}
.the-best-ad > div {
  position: absolute;
  background-color: var(--orange);
  width: 5.4rem;
  height: 80%;
  left: 8px;
  border-radius: 3rem;
  z-index: 1;
}

.hero-text {
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
  text-transform: uppercase;
  font-size: 4.5rem;
  font-weight: bold;
  color: white;
  text-overflow: inherit;
}

.hero-text-liner {
  font-size: 1rem;
  font-weight: 200;
  text-transform: none;
  letter-spacing: 1px;
  width: 80%;
}

.figures {
  display: flex;
  gap: 2rem;
}

.figures > div {
  display: flex;
  flex-direction: column;
}

.div-span-number {
  color: white;
  font-size: 2rem;
}

.div-span-text {
  color: var(--gray);
  text-transform: none;
}

.hero-button {
  display: flex;
  gap: 1rem;
  font-weight: normal;
}

.hero-button > :nth-child(1) {
  color: white;
  background-color: var(--orange);
  width: 8rem;
}

.hero-button > :nth-child(2) {
  color: white;
  background-color: transparent;
  width: 8rem;
  border: 2px solid var(--orange);
}

.right-h {
  flex: 1;
  position: relative;
  background-color: var(--orange);
}

.right-h > .btn {
  position: absolute;
  right: 3rem;
  top: 2rem;
  color: black;
}

.heart-rate {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background-color: var(--darkGrey);
  width: fit-content;
  padding: 1rem;
  align-items: start;
  border-radius: 5px;
  position: absolute;
  right: 4rem;
  top: 7rem;
}

.heart-rate > :nth-child(1) {
  width: 2rem;
}

.heart-rate > :nth-child(2) {
  color: var(--gray);
}

.heart-rate > :nth-child(3) {
  color: white;
  font-size: 1.5rem;
}

.hero_image {
  position: absolute;
  top: 10rem;
  right: 8rem;
  width: 23rem;
}

.hero_image_back {
  position: absolute;
  top: 4rem;
  right: 20rem;
  width: 15rem;
  z-index: -1;
}

.calories {
  display: flex;
  gap: 2rem;
  background-color: var(--caloryCard);
  border-radius: 5px;
  padding: 1rem;
  position: absolute;
  top: 32rem;
  right: 28rem;
}

.calories > img {
  width: 3rem;
}
.calories > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.calories > div > :nth-child(1) {
  color: var(--gray);
}

.calories > div > :nth-child(2) {
  color: white;
  font-size: 1.5rem;
}

@media screen and (max-width: 768px) {
  .hero {
    flex-direction: column;
  }

  .blur-h {
    width: 14rem;
  }

  .the-best-ad {
    margin-top: 0px;
    font-size: small;
    align-self: center;
    transform: scale(0.8);
  }

  .hero-text {
    align-items: center;
    font-size: xx-large;
    justify-content: center;
  }

  .hero-text-liner {
    font-size: small;
    font-weight: 200;
    text-align: justify;
    letter-spacing: 1px;
  }

  .hero-button {
    justify-content: center;
  }

  .figures > div > span:nth-of-type(1) {
    font: large;
  }

  .figures > div > span:nth-of-type(2) {
    font: small;
  }

  .right-h {
    display: none;
  }
}
